<template>
  <div id="box">
      <mu-container v-for="article in ArticleList" :key="article.id" style="margin: 7px 0;">
        <mu-card style="width: 100%; max-width: 375px; margin: 0 auto;">
          <mu-card-media :title="article.title" :sub-title="article.author">
            <div class="titleImg">
              <!-- <div class="photo-bg" id="head-portrait" :style="{backgroundImage: 'url(' + require('../../assets/img/'+ Imgs) + ')'}" ></div> -->
              <div class="photo-bg" id="head-portrait" :style="{backgroundImage: 'url(' + article.imgurl + ')'}" ></div>
            </div>
          </mu-card-media>
          <mu-card-text>
            <div>{{ article.guide_reading }}</div>
          </mu-card-text>
          <mu-card-actions>
            <mu-button flat @click="goPage('Details', article.id)"></mu-button>
            <!-- <input type="file" @change="addImg(article.id)"> -->
          </mu-card-actions>
        </mu-card>
      </mu-container>
  </div>
</template>
<script>
import 'muse-ui-loading/dist/muse-ui-loading.css';
import Vue from 'vue';
import Loading from 'muse-ui-loading';
Vue.use(Loading);
import Toast from 'muse-ui-toast';
Vue.use(Toast);
export default {
  name: 'ArticleList',
  data () {
    return {
      Imgs: 'back_2.jpg',
      ArticleList: [],
      index: 0,
      sign: 0,
      scroll: null,
      aBScroll: null
    }
  },
  mounted: function () {
    // window.addEventListener('scroll', this.handleScroll, true);  // 
    this.addEventListeners(window, 'scroll', this.handleScroll)
  },
  methods: {
    addEventListeners(ele,type,callback){
      if(ele.addEventListener){
        ele.addEventListener(type,callback,true);
      }else if(ele.attachEvent){
        ele.attachEvent('on' + type,callback);
      }else{
        ele['on' + type] = callback;
      }
    },
    removeEventListener(ele,type,callback){
      if(ele.removeEventListener){
          ele.removeEventListener(type,callback,false);
      }else{
          ele.detachEvent('on'+type,callback.bind(ele));
      }
    },
    getArticleList () {
      const that = this
      var loading;
      if (that.index === that.sign) {
          loading = this.$loading({
          overlayColor: 'hsla(0,0%,100%,.9)',// 
        });
      }
      this.axios.get(`${this.axios.defaults.baseURL}/article/article_list`, {params:{index:this.index}}).then((res) => {
        if (res.data.data.length !== 0) {
          for(let i = 0; i < res.data.data.length; i++) {
            this.ArticleList.push(res.data.data[i])
            this.index++
            this.sign = this.index
          };
          // this.ArticleList.reverse()
        } else {
          this.$toast.message('')
          this.sign++
        }
        if (loading) {
          loading.close()
        }

      })
    },
    // base64
    convertImgToBase64 (url, callback, outputFormat) {
      //canvas
      var canvas = document.createElement('CANVAS');
      var ctx = canvas.getContext('2d');
      //img
      var img = new Image;
      img.crossOrigin = 'Anonymous';
      //
      img.onload = function(){
          var width = img.width;
          var height = img.height;
          // 4
          var rate = (width<height ? width/height :height/width)/4;
          //widthheight
          canvas.width = width*rate;
          canvas.height = height*rate;
          //
          ctx.drawImage(img,0,0,width,height,0,0,width*rate,height*rate);
          //
          var dataURL = canvas.toDataURL(outputFormat || 'image/png');
          //
          callback.call(this, dataURL);
          //
          canvas = null;
      };
		  img.src = url;
    },
    addImg (id) {
      const that = this;
      let reader =new FileReader()
      reader.readAsDataURL(event.target.files[0])
      reader.onload = function() {
        let imgUrl = this.result
        that.convertImgToBase64 (imgUrl, function (base64Img) {
          var params = new URLSearchParams()
          params.append('imgUrl', base64Img)
          params.append('id', id)
          that.axios.post(`${that.axios.defaults.baseURL}/article/addImg`, params).then((res) => {
            that.getArticleList()
          })
        })
      };
    },
    handleScroll: function () {
        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
        var top = document.documentElement.scrollTop || document.body.scrollTop;
        let scrollObj = document.getElementById('box'); // 
        let scrollHeight = scrollObj.offsetHeight; // div 
        //
        if(top + clientHeight - 71 == scrollHeight){
          this.getArticleList();
        }
    },
    goPage(url, id) {
      this.$router.push({ name: url, query:{id: id}})
    }
  },
  beforeMount () {
    this.getArticleList()

  },
  destroyed: function () {
    const that = this
    // window.removeEventListener('scroll', that.handleScroll, true);   //  
    that.removeEventListener(window, 'scroll', that.handleScroll)
  }
}
</script>
<style scoped lang="less">
  .titleImg {
    width: 100%;
    height: 200px;
    background-repeat:no-repeat;
    background-size:100% 100%;
    -moz-background-size:100% 100%;

  }
  .photo-bg {
    width:100%;
    height:100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
    .demo-loadmore-wrap {
    width: 100%;
    max-width: 360px;
    height: 420px;
    display: flex;
    flex-direction: column;
    .mu-appbar {
      width: 100%;
    }
  }
  .demo-loadmore-content {
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
</style>



